import React, { Component } from "react"

class GetCheckList extends Component {
  render() {
    const { elementType, elementTypeFilter } = this.props
    return (
      <>
        <input
          type="checkbox"
          onChange={elementTypeFilter}
          name="elementTypeButtons"
          value={elementType}
          defaultChecked={true}
        />
        {elementType}
        <br></br>
        {/* <p>hello</p> */}
      </>
    )
  }
}

export default GetCheckList
