import React, { Component } from "react"
import Banner from "./components/Banner/Banner"
import ModalComponent from "./components/Popups/Popup"
const ProductContext = React.createContext()

const IP_ADDRESS_PG = "ws://192.168.0.101:"
const PORT = 7681
const URL = IP_ADDRESS_PG + PORT
var qaLogsButton = 1
var temp = 0
var callerFunction = ""

class InformationProvider extends Component {
  constructor() {
    super()
    this.state = {
      ws: null,
      flag: false,
      recordName: "",
      inputItems1: "",
      inputItems2: "",
      FTUE_Z_ORDER: 75,
      appium_status: "Check Appium Connection",
      zindexChoice: 1,
      elementType: [],
      elementTypeButton: [],
      getExperimentsButton: 0,
      logs: [],
      exp_logs: [],
      contentpretty: {},
      gameCommandList_list: {},
      isShown: false,
      promptMessage: "",
      promptCalled: false,
      alert_called: false,
      alert_message: "",
      alert_type: "",
      spinner_called: false
    }
    this.showcontent = this.showcontent.bind(this)
    this.hidecontent = this.hidecontent.bind(this)
  }

  componentDidMount() {
    this.connect()
  }
  timeout = 250

  connect = () => {
    var ws = new WebSocket(URL)
    let that = this // cache the this
    var connectInterval
    ws.onopen = () => {
      console.log("connected websocket main component")
      this.setState((prevState) => ({
        ...prevState,
        ws: ws,
        flag: true
      }))

      ws.send('{"command":"qaLogs"}')
      that.timeout = 250 // reset timer to 250 on open of websocket connection
      clearTimeout(connectInterval) // clear Interval on on open of websocket connection
    }

    ws.onclose = (e) => {
      console.log(
        `Socket is closed. Reconnect will be attempted in ${Math.min(
          10000 / 1000,
          (that.timeout + that.timeout) / 1000
        )} second.`,
        e.reason
      )
      this.setState((prevState) => ({
        ...prevState,
        flag: false
      }))

      that.timeout = that.timeout + that.timeout //increment retry interval
      connectInterval = setTimeout(this.check, Math.min(10000, that.timeout)) //call check function after timeout
    }

    ws.onerror = (err) => {
      console.error("Socket encountered error: ", err.message, "Closing socket")
      this.setState((prevState) => ({
        ...prevState,
        flag: false
      }))
      ws.close()
    }

    ws.onmessage = (evt) => {
      var log = JSON.parse(evt.data)
      console.log("Onmessgae")
      if (temp == 1) {
        log = JSON.parse(evt.data)["arr"]
      }
      if (temp) {
        this.setState((prevState) => ({
          ...prevState,
          exp_logs: log,
          logs: log,
          getExperimentsButton: 1
        }))
      } else {
        this.setState((prevState) => ({
          ...prevState,
          exp_logs: log,
          logs: log,
          getExperimentsButton: 0
        }))
      }
      temp = 0
    }
  }

  // TODO make prompt
  addMessage = (msg) => {
    var output = document.getElementById("output")
    console.log(msg)
    output.innerHTML += msg + "<br/>"
  }

  getFullScreenElementZIndex = (log) => {
    console.log("get Full screen ElementZindex is called")
    for (var i = 0; i < log.length; i++) {
      if (log[i]["boundbox"]["height"] >= 1920) {
        return log[i]["zindex"]
      }
    }
  }

  // TODO Make some display arrangement
  showcontent = (c) => {
    console.log("showContent is called")
    c.target.style.background = "lightgrey"
    c.target.style.border = "dotted 1px Blue"
    c.target.style.zIndex = -2
    var t_contentpretty = this.syntaxHighlight(
      c.target.getAttribute("value").replace("/'/g", '"')
    )
    document.getElementById("idname").innerHTML = t_contentpretty
  }

  // TODO make some arrangement
  hidecontent = (c) => {
    console.log("hidecontent is called")
    c.target.style.background = null
    c.target.style.border = "solid 1px grey"
    document.getElementById("idname").innerHTML = ""
  }

  syntaxHighlight = (json) => {
    console.log("syntaxHighlight is called")
    json = json
      .replace(/&/g, "&amp;")
      .replace(/</g, "&lt;")
      .replace(/>/g, "&gt;")
    return json.replace(
      /("(\\u[a-zA-Z0-9]{4}|\\[^u]|[^\\"])*"(\s*:)?|\b(true|false|null)\b|-?\d+(?:\.\d*)?(?:[eE][+\-]?\d+)?)/g,
      function (match) {
        var cls = "number"
        if (/^"/.test(match)) {
          if (/:$/.test(match)) {
            cls = "key"
          } else {
            cls = "string"
          }
        } else if (/true|false/.test(match)) {
          cls = "boolean"
        } else if (/null/.test(match)) {
          cls = "null"
        }
        return '<span class="' + cls + '">' + match + "</span>"
      }
    )
  }

  check = () => {
    const { ws } = this.state.ws
    if (!ws || ws.readyState === WebSocket.CLOSED) this.connect() //check if websocket instance is closed, if so call `connect` function.
  }

  getFullScreenElementZIndex = (log) => {
    for (var i = 0; i < log.length; i++) {
      if (log[i]["boundbox"]["height"] >= 1920) {
        return log[i]["zindex"]
      }
    }
  }

  qaLogs = (event) => {
    if (this.state.flag) {
      this.state.ws.send('{"command":"qaLogs"}')
    } else {
      this.setState((prevState) => ({
        ...prevState,
        flag: false
      }))
    }
  }

  click = (ele) => {
    console.log("click is called")
    console.log(ele.getAttribute("name"))
  }

  clicked = (ele) => {
    console.log("clicked ghgh")
    console.log(ele.target)
    var url = "action/tap_sprite"
    var uriComponent = { name: ele.target.getAttribute("name") }
    this.getRequest(url, uriComponent, true)
    this.qaLogs()
  }

  get_magic_logs() {
    console.log("magic Logs")
    var url = "action/get_magic_logs"
    var ret = this.getRequest(url, undefined, false)
  }

  elementTypeFilter = (et) => {
    console.log("elemntTypeFilter")
    var value = et.target.value
    var alldiv = document.getElementsByClassName("elements")
    for (var a = 0; a < alldiv.length; a++) {
      if (et.target.checked) {
        if (alldiv[a].getAttribute("datatype") === value) {
          alldiv[a].style.display = "block"
        }
      } else {
        if (alldiv[a].getAttribute("datatype") === value) {
          alldiv[a].style.display = "none"
        }
      }
    }
  }

  startRecord = (evt) => {
    console.log("Start Record button is clicked")
    var recordName = this.getRecordName()
    if (recordName != "") {
      console.log("started recording " + recordName)
    }
    this.state.ws.send('{"command":"recordStart"}')
    // use this to start record from cocos code
  }

  playRunButton = (evt) => {
    console.log("PlayRun button is called")
    var runName = this.playRun()
  }

  playRun = () => {
    console.log("Playrun is called")
    var startRun = ""
    let rn = prompt("Enter run name to starting playing:")
    let rc = window.confirm("Confirm run name :" + rn)
    console.log(rn, rc)
    if (rc == true && rn != "" && rn != undefined && rn != null) {
      startRun = this.getRequest("action/play_run", { name: rn }, false)
      return rn
    } else {
      alert("Error in Appium Connection Please restart Appium.")
    }
  }

  inputBtn = (evt) => {
    console.log("send message is called")
    this.sendMessage()
  }

  getRecordName = () => {
    var startRun = ""
    console.log("get RecorName is called")
    if (this.getAppiumStatus()) {
      let recordName = prompt("start Recording...Enter name for this run:")
      let confirmName = window.confirm("confirm record name :" + recordName)
      if (
        confirmName &&
        recordName != "" &&
        recordName != undefined &&
        recordName != null
      ) {
        startRun = this.getRequest(
          "action/start_record",
          { name: recordName },
          true
        )
        return recordName
      }
    } else {
      alert("Error in Appium connection. Please restart Appium.")
    }
  }

  getRequest = (url, uriComponent, reqType) => {
    console.log("Get request is called")
    var xmlHttp = new XMLHttpRequest()
    console.log(xmlHttp)
    var uriComponentString = ""
    var query = ""
    if (uriComponent != undefined) {
      if (Object.keys(uriComponent).length > 0) {
        query = "?"
      }

      var and_operator = false
      Object.keys(uriComponent).forEach(function (key) {
        console.log(key + " " + uriComponent[key])
        if (!and_operator) {
          uriComponentString +=
            key + "=" + encodeURIComponent('"' + uriComponent[key] + '"')
          and_operator = true
        } else {
          uriComponentString +=
            "&" + key + "=" + encodeURIComponent('"' + uriComponent[key] + '"')
        }
      })
      xmlHttp.open("GET", url + query + uriComponentString, reqType) // false for synchronous request
    } else {
      xmlHttp.open("GET", url + query + uriComponentString, reqType) // false for synchronous request
    }
    console.log("Send")
    xmlHttp.send()
    console.log("send")
    return xmlHttp.responseText
  }

  sendMessage = () => {
    console.log("send message is called")
    console.log(this.state.input)
    this.state.ws.send(this.state.input)
    this.setState((prevState) => ({
      ...prevState,
      input: ""
    }))
  }

  playRunButton = () => {
    console.log("playRun Button is called")
    var rn = this.playRun()
    this.addMessage(`starting play run name : $rn`)
  }

  stopRecord = () => {
    console.log("stop record is called")
    this.state.ws.send('{"command":"recordStop"}')
  }

  zindexChanged = (ele) => {
    console.log("Zindexchnaged is called")
    this.qaLogs()
  }

  startAppium = () => {
    console.log("start Appium is called")
    var url = "start_appium"
    var x = this.getAppiumStatus()

    if (x === "false") {
      this.setState((prevState) => ({
        ...prevState,
        spinner_called: true
      }))
      setImmediate(() => {
        var ret = this.getRequest(url, undefined, false)
        this.setState((prevState) => ({
          ...prevState,
          appium_status: "Appium Connected",
          spinner_called: false
        }))
        return ret
      })
    }
  }

  getAppiumStatus = () => {
    console.log("Get appium status is called")
    var url = "get_appium_status"
    var ret = this.getRequest(url, undefined, false)
    console.log(ret)
    if (ret == "true") {
      this.setState((prevState) => ({
        ...prevState,
        appium_status: "Appium Connected"
      }))
    } else {
      this.setState((prevState) => ({
        ...prevState,
        appium_status: "Start Appium"
      }))
    }
    return ret
  }

  gameCommandList = (evt) => {
    console.log("Game command list is called")
    var url = "action/get_command_list"
    var x = this.getAppiumStatus()
    var response = ""
    if (x === "true") {
      this.setState((prevState) => ({
        ...prevState,
        spinner_called: true
      }))
      setImmediate(() => {
        response = this.getRequest(url, undefined, false)
        console.log(this.state.spinner_called)
        var jsonList = JSON.parse(JSON.parse(response))
        var gameCommandHTML = ""
        var buttonCount = 0
        Object.keys(jsonList).forEach(function (key) {
          buttonCount += 1
          gameCommandHTML +=
            "<button onclick='executeGameCommand(this)' id=" +
            key +
            " type='button'>" +
            key +
            "</button>"
        })
        if (buttonCount == 0) {
          this.get_magic_logs()
        }
        this.setState((prevState) => ({
          ...prevState,
          gameCommandList_list: jsonList,
          isShown: true,
          spinner_called: false
        }))
        return gameCommandHTML
      })
    } else {
      this.setState((prevState) => ({
        ...prevState,
        alert_called: true,
        alert_message:
          "Please Connect the Appium (Appium -U adb-device-no in the terminal then click the Appium button)",
        alert_type: "error"
      }))
    }
  }

  executeGameCommand = (ele) => {
    console.log("execute game command is called")
    var name = ele.target.id
    var url = "action/" + name
    var uri = undefined
    let params = prompt("Enter url params for this command")
    if (params == undefined || params == "") {
      this.getRequest(url, uri, true)
    } else {
      this.getRequest(url + "?" + params, undefined, true)
    }
    this.qaLogs()
  }

  updateExperiment = (exp) => {
    console.log("Update experiment is called")
    var name = exp.target.id
    var x = this.getAppiumStatus()
    if (x === "true") {
      this.setState((prevState) => ({
        ...prevState,
        spinner_called: true
      }))
      var url = "action/update_experiment"
      var uri = { name: name, value: exp.target.value }
      setImmediate(() => {
        this.getRequest(url, uri, false)
        this.setState((prevState) => ({
          ...prevState,
          alert_called: true,
          alert_message: "WOW !!! experiment changed sucsessfully",
          alert_type: "success",
          spinner_called: false
        }))
      })
    } else {
      this.setState((prevState) => ({
        ...prevState,
        alert_called: true,
        alert_message:
          "Please Connect the Appium and then Change the experiment",
        alert_type: "warning"
      }))
    }
  }

  clearoutput = () => {
    console.log("clear log is called")
    this.setState((prevState) => ({
      ...prevState,
      gameCommandList_list: {},
      logs: []
    }))
  }

  getExperiments = () => {
    console.log("get Experiment is called")
    temp = 1
    this.state.ws.send('{"command":"experiments"}')
  }

  refreshPage = () => {
    window.location.reload()
  }
  handle_alert = () => {
    this.setState({ alert_called: false })
  }

  render() {
    if (!this.state.flag) {
      return (
        <Banner
          title="Please Check Your Web Socket Connection"
          subtitle="Kindly run the Game on the top of the screen"
        >
          <button type="submit" onClick={this.refreshPage}>
            Refresh Button
          </button>
        </Banner>
      )
    }

    return (
      <>
        <ProductContext.Provider
          value={{
            exp_logs: this.state.exp_logs,
            value: this.state.logs,
            flag: this.state.flag,
            FTUE_Z_ORDER: this.state.FTUE_Z_ORDER,
            zindexChoice: this.state.zindexChoice,
            appium_status: this.state.appium_status,
            elementType: [],
            getExperimentsButton: this.state.getExperimentsButton,
            qaLogs: this.qaLogs,
            clicked: this.clicked,
            showcontent: this.showcontent,
            hidecontent: this.hidecontent,
            elementTypeFilter: this.elementTypeFilter,
            clearoutput: this.clearoutput,
            getExperiments: this.getExperiments,
            getAppiumStatus: this.getAppiumStatus,
            startAppium: this.startAppium,
            gameCommandList: this.gameCommandList,
            startRecord: this.startRecord,
            stopRecord: this.stopRecord,
            playRunButton: this.playRunButton,
            updateExperiment: this.updateExperiment,
            contentpretty: this.state.contentpretty,
            gameCommandList_list: this.state.gameCommandList_list,
            executeGameCommand: this.executeGameCommand,
            isShown: this.state.isShown,
            zindexChanged: this.zindexChanged,
            alert_called: this.state.alert_called,
            alert_message: this.state.alert_message,
            alert_type: this.state.alert_type,
            handle_alert: this.handle_alert,
            alert_type: this.state.alert_type,
            spinner_called: this.state.spinner_called
          }}
        >
          {this.props.children}
        </ProductContext.Provider>
      </>
    )
  }
}

const ProductConsumer = ProductContext.Consumer
export { InformationProvider, ProductConsumer }

export default ProductContext
